:root {
  --color-main: #08a6e4;
  --color-main-shadow:rgba(7, 172, 230, 0.16);
  --color-red: #fb3b64;
  --color-green: #55dfb4;
  --color-black:#000000;
  --color-grey: #a5a5a5;
  --color-light-grey: #f7f7f7;
  --color-white: #ffffff;
  --color-contact-gray: #707070;
  --pricing-type-color: #13253B;
  --btn-radius: 35px;
  --font-raleway: "Raleway", sans-serif;
  --font-open-sans: "Open Sans", sans-serif;
}

* {
  box-sizing: border-box;
}

body {
  font-family: var(--font-open-sans);
}

a {
  text-decoration: none;
}

h2 {
  font-family: var(--font-raleway);
  font-size: 2.25rem;
  text-align: center;
}

.container {
  width: 1352px;
  margin: 0 auto;
}

/*
HEADER
*/
.header {
  box-shadow: 0 3px 6px rgba(0, 0, 0, 0.1607843137);
  height: 70px;
}

.header__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.logo {
  color: var(--color-main);
  font-size: 2rem;
  font-family: var(--font-raleway), sans-serif;
}

.header__nav {
  display: flex;
}

.nav__element {
  padding: 0 26px;
}

.nav__link {
  height: 100%;
  padding: 22px 5px;
  text-transform: uppercase;
  color: var(--color-grey);
  font-size: 0.6875rem;
  font-weight: 700;
  letter-spacing: 0.205rem;
  border-bottom: 4px solid transparent;
}

.nav__link:hover {
  border-bottom: 4px solid var(--color-main);
  color: var(--color-black);
}

/*
HERO
 */
.hero {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.hero__content {
  margin-top: 130px;
  text-align: center;
}

.hero__header {
  font-family: var(--font-raleway), sans-serif;
  font-size: 3.0625rem;
}

.hero__heading {
  margin-top: 20px;
  margin-bottom: 38px;
}

.btn a {
  margin: 100px 0;
}

.btn {
  color: var(--color-white);
  padding: 20px 40px;
  box-shadow: 5px 10px 14px var(--color-main-shadow);
  border-radius: 40px;
  display: inline-block;
}

.btn--primary {
  background-color: var(--color-main);
  font-weight: bold;
}

.btn--primary:focus, .btn--primary:hover {
  background-color: var(--color-grey);
  transition: 0.3ms;
}

.btn--large {
  padding: 22px 157px;
}

.hero__image {
  width: 100%;
  height: auto;
}

/*
STATS SECTION
 */
.stats {
  margin-top: 100px;
}

.stats__wrapper {
  max-width: 800px;
  margin: 100px auto 120px;
  display: flex;
  justify-content: space-between;
}

.stats__box {
  max-width: 160px;
  display: flex;
  flex-direction: column;
  align-items: center;
  text-align: center;
}

.stats__circle {
  border-radius: 50%;
  width: 63px;
  height: 63px;
  position: relative;
}

.stats__img {
  position: absolute;
}

.stats__box:nth-child(1) .stats__circle {
  background-color: var(--color-red);
}

.stats__box:nth-child(1) .stats__img {
  top: 3px;
  left: 50%;
}

.stats__box:nth-child(2) .stats__circle {
  background-color: var(--color-main);
}

.stats__box:nth-child(2) .stats__img {
  top: 12px;
  left: 35%;
}

.stats__box:nth-child(3) .stats__circle {
  background-color: var(--color-green);
}

.stats__box:nth-child(3) .stats__img {
  top: 6px;
  left: 48%;
}

.stats__number {
  font-weight: 700;
  letter-spacing: 0.3725rem;
  font-family: var(--font-raleway), sans-serif;
  text-align: center;
  padding: 19px 0;
  font-size: 1.25rem;
}

.stats__description {
  color: var(--color-grey);
  text-align: center;
  line-height: 1.25rem;
}

/*
BENEFITS
*/
.benefits {
  background-color: var(--color-light-grey);
  padding: 100px 0;
}

.benefits__container {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.benefits__box {
  background-color: var(--color-white);
  display: flex;
  align-items: flex-end;
  justify-content: space-between;
  margin-bottom: 37px;
  border: 1px solid #e8e9ed;
  border-radius: 5px;
}

.benefits__box:last-child {
  margin-bottom: 0;
}

.benefits__text {
  max-width: 415px;
  margin: 90px 36px;
}

.benefits__text:after {
  content: "";
  display: block;
  height: 4px;
  width: 330px;
  background-color: var(--accent-color);
  margin-top: 40px;
}

.benefits__box--notebook {
  --accent-color: var(--color-red);
}

.benefits__text:nth-child(2):after {
  background-color: var(--color-main);
}

.benefits__title {
  text-align: left;
  padding-bottom: 30px;
}

.benefits__description {
  line-height: 1.375rem;
}

.benefits__image {
  position: relative;
}

.benefits__image img {
  position: relative;
}

.benefits__box--notebook .benefits__image {
  margin: 20px 117px 38px 40px;
}

.benefits__box--notebook .benefits__image img {
  width: 386px;
  height: auto;
}

.benefits__box--notebook .benefits__image:before {
  content: "";
  width: 180px;
  height: 180px;
  background-color: var(--color-red);
  position: absolute;
  border-radius: 40px;
  transform: rotate(40deg);
  right: 0;
  top: -40px;
}

.benefits__box--phone {
  flex-direction: row-reverse;
}

.benefits__box--phone {
  --accent-color: var(--color-main);
  flex-direction: row-reverse;
  padding: 0 20px 0 95px;
}

.benefits__box--phone .benefits__image img {
  width: 426px;
  height: auto;
}

.benefits__box--phone .benefits__image:before {
  content: "";
  width: 320px;
  height: 320px;
  background-color: var(--color-main);
  position: absolute;
  border-radius: 50%;
  left: 0;
  top: -20px;
}

.benefits__double {
  --accent-color: var(--color-green);
  display: flex;
}

.benefits__double > div {
  margin-bottom: 0;
}

.benefits__box--solo {
  max-width: 620px;
}

.benefits__box--solo .benefits__text {
  max-width: none;
  margin: 70px 35px;
}

.benefits__single--image {
  flex-grow: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: var(--accent-color);
  border: none;
  margin-left: 60px;
  padding: 0 28px;
}

.benefits__single--image img {
  width: 295px;
  height: auto;
  margin-top: 55px;
}

/*
PRICES
 */
.pricing {
  display: flex;
  flex-direction: column;
  margin: 100px auto;
}

.pricing__boxes {
  display: flex;
  justify-content: space-around;
  margin-top: 50px;
}

.pricing__items {
  padding: 20px 100px;
}

.pricing__box--basic .pricing__items {
  padding-top: 48px;
}

.pricing__type {
  font-size: 1.25rem;
  font-family: var(--font-raleway), sans-serif;
  font-weight: 700;
  color: var(--pricing-type-color);
  padding: 34px 0 14px 0;
}

.pricing__price {
  font-size: 2.875rem;
  color: var(--accent-color);
}

.pricing__details, .pricing__items {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.pricing__item {
  padding: 20px 0;
}

.pricing__item:last-child {
  padding-bottom: 70px;
}

.pricing__limited {
  font-size: 0.75rem;
  font-weight: 700;
  margin-top: 15px;
  font-family: var(--font-raleway);
}

.pricing__box--basic {
  --accent-color: var(--color-red);
  border: 2px solid var(--accent-color);
  border-radius: 18px;
  position: relative;
}

.pricing__box .btn {
  position: absolute;
  bottom: -20px;
  background-color: var(--accent-color);
  left: 32%;
  font-size: 0.8125rem;
  font-weight: 600;
}

.pricing__box--professional {
  --accent-color: var(--color-main);
  border: 2px solid var(--accent-color);
  box-shadow: 5px 10px 27px rgba(7, 172, 230, 0.1607843137);
  border-radius: 18px;
  position: relative;
  width: 400px;
  margin: 0 40px;
}

.pricing__box .btn {
  position: absolute;
  bottom: -20px;
  background-color: var(--accent-color);
  left: 32%;
}

.pricing__box--professional .pricing__type {
  font-size: 1.3125rem;
}

.pricing__box--basic, .pricing__box--premium {
  margin-top: 25px;
  margin-bottom: 25px;
}

.pricing__box--professional .pricing__items {
  padding: 45px 100px;
}

.pricing__box--premium {
  --accent-color: var(--color-green);
  border: 2px solid var(--accent-color);
  border-radius: 18px;
  position: relative;
}

.pricing__box--premium .pricing__items {
  padding: 22px 100px;
}

.pricing__box .btn {
  position: absolute;
  bottom: -30px;
  background-color: var(--accent-color);
  left: 34%;
}

.pricing__item::before {
  content: url("../images/Checkmark-1.svg");
  padding-right: 10px;
}

.pricing__item--not:before {
  content: url("../images/Close.svg");
}

/*
CONTACT
 */
.contact {
  display: flex;
  margin-top: 155px;
  margin-bottom: 70px;
  max-width: 896px;
}

.contact__column {
  flex-direction: column;
  width: 50%;
}

.contact__title {
  text-align: left;
  font-size: 3.0625rem;
  font-family: var(--font-raleway);
  line-height: 3.5625rem;
  margin-bottom: 22px;
}

.contact__description {
  margin-bottom: 42px;
}

.contact__details a {
  color: var(--color-grey);
}

.contact__details a:first-child {
  margin-right: 20px;
}

.form {
  display: flex;
  flex-direction: column;
}

.form__label {
  text-transform: uppercase;
  color: var(--color-main);
  font-size: 0.6875rem;
  line-height: 0.9375rem;
  letter-spacing: 0.205rem;
  font-weight: 700;
}

.form__input {
  border: none;
  border-bottom: 1px solid var(--color-black);
  display: flex;
  width: 440px;
  height: 40px;
  margin-bottom: 30px;
}

.form__checkbox {
  display: flex;
  position: relative;
  cursor: pointer;
  margin-bottom: 12px;
  font-size: 1.375rem;
  padding-left: 35px;
}

.form__checkbox input {
  position: absolute;
  cursor: pointer;
  opacity: 0;
  height: 0;
  width: 0;
}

.form__checkbox .checkbox {
  position: absolute;
  left: 0;
  top: 4px;
  height: 24px;
  width: 24px;
  border: 1px solid var(--color-grey);
  background-color: var(--color-white);
  border-radius: 4px;
}

.form__checkbox input:checked ~ .checkbox {
  background-image: url("../images/Checkmark.svg");
  background-repeat: no-repeat;
  background-size: 50%;
  background-position: center;
}

.accept__text {
  font-size: 0.75rem;
  color: var(--color-contact-gray);
  line-height: 1.0625rem;
}

.form .btn--primary {
  font-size: 0.8125rem;
  padding: 20px 97px;
  align-self: flex-end;
  margin-top: 20px;
  border: none;
}

/*
FOOTER
 */
.footer {
  height: 90px;
  background-color: var(--color-light-grey);
  box-shadow: 0 -5px 40px rgba(0, 0, 0, 0.1607843137);
}

.footer__container {
  display: flex;
  justify-content: space-between;
  align-items: center;
  height: 100%;
}

.footer__info {
  display: flex;
  flex-direction: column;
}

.footer__copy {
  font-size: 0.6875rem;
  color: var(--color-grey);
  margin-top: 6px;
}

.footer__icons a {
  padding: 0 5px;
}